	import uniBadge from '@/components/uni-badge/uni-badge.vue'
    import {  
        mapState,  
        mapMutations  
    } from 'vuex'; 
	export default {
		data() {
			return {
				sysnum:0,
				info:[]
			}
		},
        computed: mapState(['hasLogin','userInfo']), 
		components: {uniBadge},
		onLoad() {
			this.loadData();
		},
		methods: {
            loadData() {  
				var _this = this;
				console.log('===userInfo==='+this.hasLogin);
				console.log(this.userInfo);
				console.log(this.userInfo.token);
                if (!this.hasLogin) {  
					uni.navigateTo({
						url:'login',
					})
                }else{
					_this.$Request.post('member_message/msgnum').then(res => {
						if (res.code == 200) {
							console.log(res);
							_this.info = res.datas.info;
							_this.sysnum = res.datas.info.sysnum;
							
						}
					});
				}
            },
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			toBack: function() {
				uni.switchTab({
					url:"/pages/member/home", 
					success (res) {
							const currentPages = getCurrentPages()  
							console.log(currentPages[0]);  
							currentPages[0].onLoad({  
								...currentPages[0].options  
							})
					}
				}) 
				
			}

		}
	}
